.block-editor-block-inspector .components-base-control:last-child {
  width: 100%; }

.block-editor-block-inspector input.components-range-control__slider {
  position: relative !important;
  width: auto !important; }

.block-editor-block-inspector .acpt-inline-block {
  display: flex;
  gap: 6px;
  align-items: center; }

.block-editor-block-inspector .acpt-badge {
  color: #fff;
  padding: 4px 8px;
  border-radius: 3px;
  background: black;
  margin: 0; }
  .block-editor-block-inspector .acpt-badge.acpt-badge-primary {
    background: #007cba; }
  .block-editor-block-inspector .acpt-badge.acpt-badge-success {
    background: #02c39a; }
  .block-editor-block-inspector .acpt-badge.acpt-badge-success-outline {
    background: transparent;
    border: 1px solid #02c39a;
    color: #02c39a;
    box-sizing: border-box;
    -moz-box-sizing: border-box;
    -webkit-box-sizing: border-box; }
  .block-editor-block-inspector .acpt-badge.acpt-badge-secondary {
    background: #323232; }
  .block-editor-block-inspector .acpt-badge.acpt-badge-secondary-outline {
    background: transparent;
    border: 1px solid #323232;
    color: #323232;
    box-sizing: border-box;
    -moz-box-sizing: border-box;
    -webkit-box-sizing: border-box; }
